<script>
  export default {
    data() {
      return {

      }
    },
    methods: {
      showDetail1(){
        $('.circlename').each(function (key,val) {
          $(val).removeClass('tstactive')
        })
        $('.circlename1').addClass('tstactive')
        $('.tstcircle1').css('background','#069bee')
        $('.tstcircle2').css('background','#fff')
        $('.tstcircle3').css('background','#fff')
        $('.det_tst_count1').show(200)
        $('.det_tst_zizhi2').hide(200)
        $('.det_tst_call3').hide(200)
      },
      showzizhi2(){
        $('.circlename').each(function (key,val) {
          $(val).removeClass('tstactive')
        })
        $('.circlename2').addClass('tstactive')
        $('.tstcircle1').css('background','#fff')
        $('.tstcircle2').css('background','#069bee')
        $('.tstcircle3').css('background','#fff')
        $('.det_tst_count1').hide(200)
        $('.det_tst_zizhi2').show(200)
        $('.det_tst_call3').hide(200)
      },
      showCall3(){
        $('.circlename').each(function (key,val) {
          $(val).removeClass('tstactive')
        })
        $('.circlename3').addClass('tstactive')
        $('.tstcircle1').css('background','#fff')
        $('.tstcircle2').css('background','#fff')
        $('.tstcircle3').css('background','#069bee')
        $('.det_tst_count1').hide(200)
        $('.det_tst_zizhi2').hide(200)
        $('.det_tst_call3').show(200)
      },

    },
    mounted() {
      $('.tstcircle1').css({'background':'#fff'})
      $('.tstcircle3').css({'background':'#fff'})
      $('.det_tst_count1').hide()
      $('.det_tst_zizhi2').show()
      $('.det_tst_call3').hide()
    },
    created(){

    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  .tst_ours
    .program
      height 420px
      padding-top 80px
      background url("/static/img/lianxi.png") no-repeat center
      background-size cover
      .program_img
        margin-top 160px
        width 500px
        color white
        p
          font-size 15px
    .detail
      /*height 800px*/
      padding-top 20px
      padding-bottom 20px
      width 100%
      background #fff
      .w
        height 100%
        .detail_left
          width 20%
          height 200px
          margin-top 50px
          position relative
          .callOusline
            width 100%
            height 20px
            .callOusline_in
              width 100%
              .tstcircle2
                background #069bee
              .tstcircle
                width 20px
                height 20px
                border-radius 50%
                border 1px solid #069bee
              .circlename
                height 20px
                padding-left 10px
                cursor pointer
                line-height 20px
                &:hover
                  color #069bee
                &.tstactive
                  color #069bee
          .circleline
            width 1px
            height 70px
            margin-left 10px
            background #069bee

          .deta_click
            position absolute
            width 100%
            height 100%
            z-index 99
            .deta_click_top
              position absolute
              height 20%
              width 100%
              top 0
              cursor pointer
            .deta_click_bot
              position absolute
              height 20%
              width 100%
              bottom 0
              cursor pointer
        .detail_right
          width 80%
          height 100%
          background #fff
          .det_tst
            .det_tst_tit
              width 100%
              height 30px
              p
                text-align center
                font-size 18px
                color #000
                line-height 30px
                font-weight 700
            .det_tst_count1
              display none
              padding-top 10px
              p
                width 100%
                &:nth-of-type(odd)
                  font-size 16px
                  font-weight 700
                &:nth-of-type(even)
                  font-size 15px
                  text-indent 2em
            .det_tst_zizhi2
              display block
              height 580px
              padding-top 20px
              .p2
                padding-top 30px
                span
                  font-size 16px
              img
                width 100%
            .det_tst_call3
              display none
              width 100%
              /*height 500px*/
              background #fff
              .fenbu
                margin-top 20px
                width 100%
                border-bottom 1px solid #ccc
                &:nth-child(1)
                  margin-top 0px
                &:last-child
                  border-bottom none


</style>



